<script setup>
import { ref, computed, onMounted, nextTick } from "vue";

const emit = defineEmits(["success"]);

// é¢˜åº“
const questionBank = [
    {
        question: "ä»¥ä¸‹å“ªä¸€ä¸ªé›¶é£Ÿbingoåœ¨é›¶é£ŸåŒºçœ‹åˆ°äº†ä¼šä¼˜å…ˆæ‹¿èµ°ï¼Ÿ",
        options: ["å¤å‡¤çˆª", "åšå£«ç‰›ç­‹", "éœ¸ç‹ä¸", "ç•ªè–¯å¹²"],
        answer: "å¤å‡¤çˆª",
    },
    {
        question: "æ³°å±±çš„æ³¡æ³¡ç›ç‰¹å±±æœ€é«˜çš„æ˜¯å“ªä¸ªï¼Ÿ",
        options: ["Labubu", "äººæ°‘å¸è¾¹è§’æ–™", "æ³°å±±"],
        answer: "æ³°å±±",
    },
    {
        question: "zeroçš„æœ¬åæ˜¯ä»€ä¹ˆï¼Ÿ",
        options: ["éƒ‘å’é›¶", "éƒ‘ä¹‹æ¥¼", "éƒ‘å’æŸ”"],
        answer: "éƒ‘å’æŸ”",
    },
    {
        question: "å®å¡«çš„ç—£åœ¨è„¸çš„å“ªè¾¹ï¼Ÿ",
        options: ["å·¦è¾¹", "å³è¾¹"],
        answer: "å³è¾¹",
    },
    {
        question: "å†›å“¥å»KTVå¿…ç‚¹çš„æ­Œæ˜¯ä»€ä¹ˆæ­Œï¼Ÿ",
        options: ["æ˜¥å¤©èŠ±ä¼šå¼€", "æˆ‘æ˜¯ä¸€åªé±¼", "ä¸€åƒä¸ªä¼¤å¿ƒçš„ç†ç”±"],
        answer: "æˆ‘æ˜¯ä¸€åªé±¼",
    },
];

// æ¸¸æˆçŠ¶æ€
const selectedQuestions = ref([]);
const currentQuestionIndex = ref(0);
const selectedAnswer = ref("");
const showAnswer = ref(false);
const isCorrect = ref(false);
const correctCount = ref(0);
const quizCompleted = ref(false);
const answerResults = ref([]);

// å¯†ç è¾“å…¥
const passwordChars = ref(["", "", "", ""]);
const passwordInputs = ref([]);
const passwordError = ref("");
const showVideo = ref(false);
const videoPlayer = ref(null);

// å¯¼å…¥è§†é¢‘
const videoUrl = new URL(
    "../assets/video/é£ä¹¦20251024-151425.mp4",
    import.meta.url,
).href;

const currentQuestion = computed(() => {
    return selectedQuestions.value[currentQuestionIndex.value] || {};
});

const isPasswordComplete = computed(() => {
    return passwordChars.value.every((char) => char.length === 1);
});

// åˆå§‹åŒ–æ¸¸æˆï¼šéšæœºæŠ½å–4é“é¢˜
const initGame = () => {
    const shuffled = [...questionBank].sort(() => Math.random() - 0.5);
    selectedQuestions.value = shuffled.slice(0, 4);
    currentQuestionIndex.value = 0;
    selectedAnswer.value = "";
    showAnswer.value = false;
    isCorrect.value = false;
    correctCount.value = 0;
    quizCompleted.value = false;
    answerResults.value = [];
    passwordChars.value = ["", "", "", ""];
    passwordError.value = "";
    showVideo.value = false;
};

// é€‰æ‹©ç­”æ¡ˆ
const selectAnswer = (option) => {
    if (!showAnswer.value) {
        selectedAnswer.value = option;
    }
};

// æäº¤ç­”æ¡ˆ
const submitAnswer = () => {
    if (!selectedAnswer.value) return;

    showAnswer.value = true;
    isCorrect.value = selectedAnswer.value === currentQuestion.value.answer;

    if (isCorrect.value) {
        correctCount.value++;
    }

    answerResults.value.push({
        question: currentQuestion.value.question,
        correct: isCorrect.value,
        userAnswer: selectedAnswer.value,
        correctAnswer: currentQuestion.value.answer,
    });

    // 3ç§’åè‡ªåŠ¨è¿›å…¥ä¸‹ä¸€é¢˜æˆ–å®Œæˆ
    setTimeout(() => {
        if (currentQuestionIndex.value < selectedQuestions.value.length - 1) {
            nextQuestion();
        } else {
            completeQuiz();
        }
    }, 3000);
};

// ä¸‹ä¸€é¢˜
const nextQuestion = () => {
    currentQuestionIndex.value++;
    selectedAnswer.value = "";
    showAnswer.value = false;
    isCorrect.value = false;
};

// å®Œæˆç­”é¢˜
const completeQuiz = () => {
    quizCompleted.value = true;

    // å¦‚æœå…¨å¯¹ï¼Œè‡ªåŠ¨èšç„¦åˆ°ç¬¬ä¸€ä¸ªå¯†ç è¾“å…¥æ¡†
    if (correctCount.value === 4) {
        nextTick(() => {
            if (passwordInputs.value[0]) {
                passwordInputs.value[0].focus();
            }
        });
    }
};

// é‡æ–°æŒ‘æˆ˜
const retryQuiz = () => {
    showVideo.value = false;
    if (videoPlayer.value) {
        videoPlayer.value.pause();
        videoPlayer.value.currentTime = 0;
    }
    initGame();
};

// å¤„ç†å¯†ç è¾“å…¥
const handlePasswordInput = (index) => {
    // é™åˆ¶åªèƒ½è¾“å…¥ä¸€ä¸ªå­—ç¬¦
    if (passwordChars.value[index].length > 1) {
        passwordChars.value[index] = passwordChars.value[index].slice(0, 1);
    }

    // è‡ªåŠ¨èšç„¦åˆ°ä¸‹ä¸€ä¸ªè¾“å…¥æ¡†
    if (passwordChars.value[index].length === 1 && index < 3) {
        nextTick(() => {
            if (passwordInputs.value[index + 1]) {
                passwordInputs.value[index + 1].focus();
            }
        });
    }

    passwordError.value = "";
};

// å¤„ç†é€€æ ¼é”®
const handleBackspace = (index) => {
    if (passwordChars.value[index] === "" && index > 0) {
        nextTick(() => {
            if (passwordInputs.value[index - 1]) {
                passwordInputs.value[index - 1].focus();
            }
        });
    }
};

// éªŒè¯å¯†ç  - ä»»ä½•å¯†ç éƒ½èƒ½é€šè¿‡
const verifyPassword = () => {
    const enteredPassword = passwordChars.value.join("");

    // ä»»ä½•4ä½å¯†ç éƒ½èƒ½é€šè¿‡ï¼Œæ’­æ”¾è§†é¢‘
    if (enteredPassword.length === 4) {
        showVideo.value = true;
        // ç¡®ä¿è§†é¢‘å…ƒç´ åŠ è½½åå†æ’­æ”¾
        nextTick(() => {
            if (videoPlayer.value) {
                videoPlayer.value.play().catch((err) => {
                    console.log("è§†é¢‘è‡ªåŠ¨æ’­æ”¾å¤±è´¥:", err);
                });
            }
        });
    } else {
        passwordError.value = "è¯·è¾“å…¥4ä½å¯†ç ";
    }
};

// è§†é¢‘æ’­æ”¾ç»“æŸ
const handleVideoEnd = () => {
    // è§†é¢‘æ’­æ”¾å®Œæˆåï¼Œè¿›å…¥æˆåŠŸé¡µé¢
    emit("success", {
        correctCount: correctCount.value,
        totalQuestions: 4,
        results: answerResults.value,
    });
};

// è·³è¿‡è§†é¢‘
const skipVideo = () => {
    if (videoPlayer.value) {
        videoPlayer.value.pause();
    }
    emit("success", {
        correctCount: correctCount.value,
        totalQuestions: 4,
        results: answerResults.value,
    });
};

onMounted(() => {
    initGame();
});
</script>

